body {
  margin: 0;
  font-size: 16px;
  min-width: 320px;
  position: relative;
  line-height: 1.65;
  font-family: "Montserrat", sans-serif;
  overflow-x: hidden;
  color: #303030;
}

body input,
body textarea {
  border: #666 1px solid;
  outline: none;
}

b,
strong {
  font-weight: 700;
}

.container {
  display: flex;
  max-width: 1656px;
  margin: 0 auto;
  padding: 0 15px;
}

.btn {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  border: none;
  background-color: transparent;
  width: fit-content;
  padding: 12px 70px;
  font-weight: 600;
  font-size: 19px;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.btn-primary {
  background-color: #26dcf2;
  color: #fff;
  box-shadow: 0 0 10px 0 rgba(28, 177, 196, 0.85);
  border-radius: 5px;
  transition: 300ms linear all;
}

.btn-primary:hover {
  box-shadow: 0 0 17px 0 rgba(28, 177, 196, 0.85);
  background-color: #0ed8f0;
}

.title {
  position: relative;
  font-weight: 700;
  font-size: 24px;
  padding-bottom: 40px;
  text-transform: uppercase;
}

.title::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 140px;
  height: 40px;
  background: transparent center center no-repeat;
  background-size: contain;
}

.title-primary {
  color: #140f8e;
}

.title-primary::after {
  background-image: url("../img/primary-header_waves.svg");
}

.title-secondary {
  color: #fff;
}

.title-secondary::after {
  background-image: url("../img/secondary-header_waves.svg");
}

.anim {
  opacity: 0;
  animation-play-state: paused;
}

.anim-action {
  opacity: 1;
  animation-play-state: running;
  animation-duration: 600ms;
  animation-timing-function: linear;
}

.anim-delay {
  animation-delay: 500ms;
}

.header {
  position: relative;
  background: #26dcf2 url("../img/header-bg.jpg") center center no-repeat;
  background-size: cover;
  padding-bottom: 50px;
  overflow: hidden;
}

.header .container {
  position: relative;
  flex-direction: column;
}

.header-atoms__img {
  position: absolute;
  width: 100%;
  top: 10%;
  left: 20%;
  animation: rotate 150s linear infinite;
}

.header-intro {
  display: flex;
  flex-direction: column;
  width: 100%;
  align-self: center;
}

.header-intro__title {
  font-size: 40px;
  line-height: 52px;
  font-weight: 600;
  color: #fff;
  margin-bottom: 0;
}

.header-intro__subtitle {
  font-weight: 200;
  font-size: 20px;
  color: #fff;
  margin-top: 0;
  margin-bottom: 40px;
}

.header-scroll__link {
  position: absolute;
  bottom: -50px;
  right: 40px;
  display: none;
  align-items: center;
  font-weight: 200;
  font-size: 11px;
  text-transform: uppercase;
  color: #fff;
  text-decoration: none;
  transform: rotate(-90deg);
  transition: 200ms linear bottom;
}

.header-scroll__link-icon {
  transform: rotate(90deg);
  margin-right: 40px;
  width: 11px;
}

.header-scroll__link:hover {
  bottom: -55px;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 20px 0;
}

.navbar-wrap {
  position: fixed;
  z-index: 999999;
  top: 0;
  left: 0;
  right: 0;
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: nowrap;
  padding-left: 0;
  padding-bottom: 25px;
  padding-top: 80px;
  background-color: #fff;
  margin-top: 0;
  overflow-y: auto;
  transform: translateX(200%);
  transition: 0.5s cubic-bezier(0.77, 0.2, 0.05, 1) transform;
  box-sizing: border-box;
}

.navbar-wrap__active {
  transform: translateX(0);
}

.navbar-item {
  list-style: none;
}

.navbar-item__link {
  display: block;
  position: relative;
  font-weight: 600;
  font-size: 18px;
  padding: 15px 0;
  text-decoration: none;
  color: #140f8e;
  text-transform: uppercase;
}

.navbar-item__link::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  height: 1px;
  width: 0;
  background-color: #fff;
  transition: 200ms linear width;
}

.navbar-item__link:hover::after {
  width: 80%;
  width: calc(100% - 50px);
}

.navbar-logo {
  width: 100%;
  object-fit: contain;
  object-position: left;
}

.navbar-logo__wrap {
  width: 50%;
}

.navbar-logo__link {
  display: flex;
  justify-content: center;
  width: 100%;
}

.navbar-btn {
  display: block;
  transform-origin: 0% 0%;
  transition: 300ms linear all;
}

.navbar-btn span {
  display: block;
  width: 33px;
  height: 4px;
  margin-bottom: 5px;
  position: relative;
  background: #fff;
  border-radius: 3px;
  z-index: 1;
  transform-origin: 4px 0;
  transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), background-color 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), opacity 0.55s ease;
}

.navbar-btn span:first-child {
  transform-origin: 0% 0%;
}

.navbar-btn span:nth-last-child(2) {
  transform-origin: 0% 100%;
}

.navbar-btn__active {
  position: fixed;
  right: 15px;
  z-index: 9999999;
}

.navbar-btn__active > span {
  opacity: 1;
  transform: rotate(45deg) translate(-22px, -19px);
  background-color: #140f8e;
}

.navbar-btn__active > span:nth-last-child(3) {
  opacity: 0;
  transform: rotate(0deg) scale(0.2, 0.2);
}

.navbar-btn__active > span:nth-last-child(2) {
  transform: rotate(-45deg) translate(0, -1px);
}

@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.about {
  position: relative;
  padding: 50px 0;
  overflow: hidden;
  min-height: 480px;
  background-color: #88a0d9;
}

.about::before {
  content: "";
  display: none;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 60vw;
  background-color: #88a0d9;
}

.about-tabs {
  position: relative;
  flex-direction: column;
  flex-wrap: wrap;
}

.about-tabs__controls {
  display: flex;
  flex-wrap: nowrap;
  position: relative;
  width: 100%;
  padding-left: 0;
  list-style: none;
  overflow-x: auto;
}

.about-tabs__controls::before {
  display: block;
  content: "";
  position: absolute;
  top: -35px;
  left: 0;
  background: transparent url("../img/atoms-icon-2.svg") center center no-repeat;
  background-size: contain;
  height: 30px;
  width: 30px;
  animation: rotate 10s linear infinite;
}

.about-tabs__controls-item {
  cursor: pointer;
  position: relative;
  font-weight: 200;
  color: #fff;
  font-size: 18px;
  transition: 300ms linear font-weight;
  text-align: center;
  padding: 15px 0;
  margin: 0 25px;
  min-width: fit-content;
}

.about-tabs__controls-item::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  height: 1px;
  width: 0;
  background-color: #fff;
  transition: 300ms linear width;
}

.about-tabs__controls-item::before {
  display: none;
  content: "";
  position: absolute;
  left: -40px;
  background: transparent url("../img/atoms-icon-2.svg") center center no-repeat;
  background-size: contain;
  transform: translateY(0);
  height: 0;
  width: 0;
  transition: 400ms linear all;
  opacity: 0;
}

.about-tabs__controls-item:hover::after {
  width: 100%;
}

.about-tabs__controls .active {
  font-weight: 500;
}

.about-tabs__controls .active::after {
  width: 100%;
}

.about-tabs__controls .active::before {
  opacity: 1;
  transform: translateY(-50%);
  left: -70px;
  height: 40px;
  width: 40px;
  animation: rotate 10s linear infinite;
}

.about-tabs__content {
  position: relative;
  width: 100%;
}

.about-tabs__content-item {
  display: flex;
  flex: 1 auto;
  flex-direction: column;
  position: absolute;
  top: 0;
  width: 100%;
  background-color: #fff;
  overflow: hidden;
  border-radius: 9px;
  box-shadow: 0 0 25px 0 rgba(0, 0, 0, 0.16);
  transform: translateY(0);
  z-index: 0;
  opacity: 0;
  transition: opacity 500ms ease-in;
}

.about-tabs__content .active {
  position: relative;
  z-index: 1;
  top: 0;
  opacity: 1;
  transition-delay: 400ms;
}

.about-tabs__content .item-description {
  position: relative;
  z-index: 1;
  width: 100%;
  padding: 15px;
  padding-bottom: 68px;
  height: auto;
  order: 2;
  overflow-x: hidden;
  overflow-y: inherit;
  box-sizing: border-box;
}

.about-tabs__content .item-description p {
  font-weight: 200;
  font-size: 14px;
  color: #4e627a;
  box-sizing: border-box;
  text-align: justify;
}

.about-tabs__content .item-description::after {
  content: "";
  position: absolute;
  z-index: 3;
  left: 15px;
  bottom: 20px;
  background: transparent url("../img/wave-icon.svg") left center no-repeat;
  background-size: contain;
  min-height: 40px;
  width: 100%;
}

.about-tabs__content .item-preview {
  width: 100%;
  height: 300px;
  object-fit: cover;
  order: 1;
}

