    bottom: 50px;
  }
}

@media (min-width: 1200px) {
  .navbar-item__link {
    font-size: 14px;
    padding: 15px 25px;
  }

  .product-description .title {
    margin-top: 100px;
  }
}

@media (min-width: 1400px) {
  .about::before {
    display: block;
  }

  .about-tabs__controls::before {
    display: none;
  }

  .about-tabs__controls-item::before {
    display: block;
  }

  .about-tabs__controls-item:hover::after {
    width: 80%;
  }

  .about-tabs__controls-item {
    text-align: left;
    width: 100%;
    padding: 10px 0;
    margin: 15px 0;
  }

  .about-tabs__controls .active::after {
    width: 80%;
  }

  .about-tabs__controls {
    display: block;
    width: 21%;
  }

  .about-tabs__content-item {
    top: 50%;
    right: 20px;
    width: 70%;
    transform: translateY(-50%);
  }

  .about-tabs__content .active {
    top: 50%;
    position: absolute;
  }

  .about-tabs__content .item-description::after {
    min-height: 60px;
    bottom: 20px;
  }

  .about-tabs__content {
    position: initial;
    width: 79%;
  }

  .about-tabs {
    flex-direction: row;
  }

  .about {
    background-color: #fff;
    padding-top: 150px;
    padding-bottom: 100px;
  }
}